import * as React from 'react';
import { useParams } from 'react-router-dom';

import Staff from '@miq/staffjs';
import { useDocumentTitle, useRequest } from '@miq/hookjs';
import { Loading } from '@miq/componentjs';
// import Form, { useForm } from '@miq/formjs';
import { Response } from '@miq/utiljs';
import { Customer } from './models';
import { CustomerCard } from './components';

export default function CustomerUpdateView() {
  useDocumentTitle('Update customer - Sales');
  const { custSlug } = useParams();

  const { res, setRes, loading, ...request } = useRequest(() => Customer({ slug: custSlug }).retrieve(), {
    refreshDeps: [custSlug],
    // onSuccess: ({ data }) => form.setValues(mapData2Values(data)),
    onError: ({ response }) => {
      // if (response?.status === 404) return navigate('../');
    },
  });

  if (loading) return <Loading />;

  const r = new Response(res);
  if (!r.isSuccess) return null;

  const inst = Customer(r.data);

  return <Staff.View title={<CustomerCard data={r.data} />} back="/staff/sales/customers"></Staff.View>;
}
